.navigation {
    position: fixed;
    width: 100vw;
    height: -webkit-fill-available;
    z-index: 99;

    ul {
        display: none;
    }

    .menu-icon {
        position: absolute;
        right: 1rem;
        top: 1rem;
        height: 2rem;
        width: 2rem;
        overflow: hidden;
        cursor: pointer;
        z-index: 999;

        .fa-bars,
        .fa-times {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 2rem;
            color: var(--primary-color);
        }
    }

    .active {
        top: 0;
        height: 100%;
        display: grid;
        opacity: .9;
        grid-template-columns: repeat(5, 1fr);

        li {
            display: grid;
            grid-template-columns: 100%;
            grid-template-rows: 45% 55%;
            background: linear-gradient(0deg, rgba(9,33,71,1) 10%, rgba(167,170,188,1) 100%);

            &:not(:last-child) {
                border-right: 1px solid rgba(9,33,71,.3);
            }

            a {
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 1.5rem;

                svg {
                    color: var(--primary-color)
                }
            }

            h2 {
                writing-mode: vertical-rl;
                display: flex;
                align-items: center;
                transform: rotate(180deg);
                font-family: 'Poppins';
                font-size: 2.5rem;
                letter-spacing: 0.2rem;
                color: var(--white);

                span {
                    font-size: 3rem;
                    letter-spacing: 0rem;
                    transform: translateY(-5px);
                }
            }

            // ANIMATION

            &:first-child {
                transform: translateX(500%);
                animation: rightLeft 1s forwards;
            }

            &:nth-child(2) {
                transform: translateX(1000%);
                animation: rightLeft 1s forwards;
            }

            &:nth-child(3) {
                transform: translateX(1500%);
                animation: rightLeft 1s forwards;
            }

            &:nth-child(4) {
                transform: translateX(2000%);
                animation: rightLeft 1s forwards;
            }

            &:last-child {
                transform: translateX(2500%);
                animation: rightLeft 1s forwards;
            }
        }
    }
}

@keyframes rightLeft {
    80% {
        transform: translateX(-50%);
    }

    100% {
        transform: translateX(0);
    }
}