:root {
    --white: whitesmoke;
    --primary-color: #092147;
    --secondary-color: #773d44;
    --tertiary-color: #a7aabc;
    --quaternary-color: #440e19;
    --quinary-color: #66B0F8;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;

    li {
        list-style-type: none;
    }
}