.home {
    height: 100vh;
    width: 100vw;
    background: url(../../../../public/assets/images/layout.webp) center/cover fixed no-repeat;

    h1 {
        position: absolute;
        color: var(--primary-color);
        font-family: 'Inria Serif';
        writing-mode: vertical-lr;
        transform: rotate(180deg);
        font-size: 8rem;
        top: -10px;
        left: -40px;
    }
}